import React from 'react'
import { Link } from 'react-router-dom'

import logo from '../../assets/images/Logoo.png';

import $ from 'jquery'




export default function Navbar() {

  $(".nav-link").on("click" , function() {

    window.location.href = `/${$(this).data("mega")}`
  })

  return <>
    <nav className="navbar navbar-expand-lg custom-navbar">
      <Link className="navbar-brand">
        <img src={logo} alt="logo" />
      </Link>

      <button className="btn navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample" aria-controls="offcanvasExample">
        <i className="fa-solid fa-bars"></i>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" data-mega="#header" to="">الرئيسية</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" data-mega="#main1" to="">عن نظام المفقودات</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" data-mega="#main2" to="">آلية العمل</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" data-mega="#main3" to="">العملاء </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" data-mega="#main4" to="">إتصل بنا </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link diff" to="#">بوابة الموظين  </Link>
          </li>

          <li className="nav-item dropdown">
            <Link className="nav-link dropdown-toggle" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              اللغة العربية
            </Link>
            <ul className="dropdown-menu custom-dropdown-menu">
              <li><Link className="dropdown-item" to="#">
                <div className="lang">
                  Eng
                  <i className="fa-solid fa-globe"></i>
                </div>

              </Link></li>

            </ul>
          </li>
        </ul>

      </div>
    </nav>
  </>

}
