import React from 'react';


import logo from '../../assets/images/Logoo.png';
import signature from '../../assets/images/website-signature-ar.png';
import GadaraLogo from '../../assets/images/gadaraLogo.png';

import { Link } from 'react-router-dom';

export default function Footer() {
  return <>
    <div className="footer py-3">
      <div className="container">
        <div className="row">
          <div className="col-md-2 col-6">
            <Link to="home">
              <img src={logo} alt="logo" className='w-100' />
            </Link>
          </div>
          <div className="col-md-8 d-flex align-items-center">
            <ul className="footer-nav list-unstyled  p-0">
              <li className="nav-item">
                <Link className='nav-link' to="">الرئيسية</Link>
              </li>
              <li className="nav-item">
                <Link className='nav-link' to="">عن نظام المفقودات</Link>
              </li>
              <li className="nav-item">
                <Link className='nav-link' to="">آلية العمل</Link>
              </li>
              <li className="nav-item">
                <Link className='nav-link' to="">العملاء</Link>
              </li>
              <li className="nav-item">
                <Link className='nav-link' to="">اتصل بنا</Link>
              </li>
            </ul>
          </div>
          <div className="col-md-2 text-start d-flex align-items-center justify-content-end">
            <div className="social-media">
              <span>
               <Link to="">
               <i className="fa-brands fa-square-facebook"></i>
               </Link>
              </span>
              <span>
                <Link to="">
                <i className="fa-brands fa-square-twitter"></i>

                </Link>
              </span>
              <span>
                <Link to="">
                <i className="fa-brands fa-linkedin"></i>

                </Link>
              </span>
            </div>
          </div>

        </div>
      </div>
    </div>
    <div className="footer_bottom py-3">
      <div className="container">
        <div className="row">
          <div className="col-lg-6 col-12">
            <div className="gadara_logo">
              <span>تصميم وتطوير</span>
              <div className="logo">
                <img src={GadaraLogo} alt="logo" />
              </div>
            </div>
          </div>
          <div className="col-lg-6 col-12 text-end">
            <div className="gadara_logo justify-content-lg-end">
              <div className="logo">
                <img src={signature} alt="logo" />
              </div>
              <span> جميع الحقوق محفوظة
                &copy;

              </span>
              <span>
                لنظام المفقودات 2022

              </span>

            </div>
          </div>
        </div>
      </div>
    </div>
  </>


}
