import React from 'react';

import questionImg from '../../assets/images/question-mark.png';
import img2 from '../../assets/images/22.png'

export default function About() {
  return <>
    <div className="about py-5" id='main1'>
      <div className="container">
        <div className="row">
          <div className="col-lg-7 col-md-7 col-12">
              <h2 className="heading">
              نبذة عن نظام المفقودات
              </h2>
              <ul className='list-unstyled'>
                <li className="list-item">
                  <div className="icon">
                  <img src={questionImg} alt="questionIcon" />
                  </div>
                  <p className='text'>
                  نظام شامل للمفقودات التى يتم ايجادها فى محطات المترو والباصات وغيرها عن طريق موظفين الامن
                  </p>
                  
                </li>
                <li className="list-item">
                  <div className="icon">
                  <img src={questionImg} alt="questionIcon" />
                  </div>
                  <p className='text'>
                  تصنيفات لا نهائية للمفقودات (الجولات - النقود - مقتنيات ذهبية - اكسسوارات- كتب ومستندات) وغيرها 
                                   </p>
                  
                </li>
                <li className="list-item">
                  <div className="icon">
                  <img src={questionImg} alt="questionIcon" />
                  </div>
                  <p className='text'>
                  يعمل النظام على إضافة المفقود داخل المنصة مع توليد باركود خاص بكل قطعة واضافة بيانات القطعة مثل(تحديد مكان الفقد - تاريخ الفقد- إضافة شرح وبيانات كتابية عن القطعة -إضافة الصور الخاصة بالقطعة - تحديد مكان التخزين) وغيرها
                    </p>
                  
                </li>
                <li className="list-item">
                  <div className="icon">
                  <img src={questionImg} alt="questionIcon" />
                  </div>
                  <p className='text'>
                  نظام احصائيات وتقديم مجموعة من التقارير مثل ( التقارير الأسبوعية والشهرية والسنوية عن المفقودات - المفقودات المسلمة - مدخلات الموظفين - أماكن ومحطات                     </p>
                  
                </li>
              </ul>
          </div>
          <div className="col-lg-5 col-md-5  col-12">
            <img src={img2} alt="img" className='w-100' />
          </div>
        </div>
      </div>
    </div>
  </>

}
