import React from 'react';

import img1 from '../../assets/images/customers1.png';
import img2 from '../../assets/images/customers2.png';
import img3 from '../../assets/images/customers3.png';
import img4 from '../../assets/images/customers4.png';
import img5 from '../../assets/images/customers5.png';

export default function Customers() {
  return <>
    <div className="customers py-5" id='main3'>
        <div className="container">
        <div className="customers_images">
            <div className="img">
                <img src={img1} alt="customers" />
            </div>
            <div className="img">
                <img src={img2} alt="customers" />
            </div>
            <div className="img">
                <img src={img3} alt="customers" />
            </div>
            <div className="img">
                <img src={img4} alt="customers" />
            </div>
            <div className="img">
                <img src={img5} alt="customers" />
            </div>
        </div>
        </div>
    </div>
  </>

}
