import React from 'react';

import logo from '../../assets/images/Logoo.png';
import { Link } from 'react-router-dom'




export default function Sidebar() {
  return <> 
  <div className="offcanvas custom-offcanvas offcanvas-end" tabIndex="-1" id="offcanvasExample" aria-labelledby="offcanvasExampleLabel">
    <div className="offcanvas-header">
      <button type="button" className="btn colse-btn" data-bs-dismiss="offcanvas" aria-label="Close">
        <i className="fa-regular fa-circle-xmark"></i>
      </button>
      <div className="logo">
        <img src={logo} alt={logo} />
      </div>
    </div>

    <div className="offcanvas-body">
      <ul className="list-unstyled p-0">
        <li className="nav-item">
          <Link to="" className='nav-link'>الرئيسية</Link>
        </li>
        <li className="nav-item">
          <Link to="" className='nav-link'>عن نظام المفقودات</Link>
        </li>
        <li className="nav-item">
          <Link to="" className='nav-link'>ألية العمل</Link>
        </li>
        <li className="nav-item">
          <Link to="" className='nav-link'> العملاء</Link>
        </li>
        <li className="nav-item">
          <Link to="" className='nav-link'> إتصل بنا</Link>
        </li>
        <li className="nav-item">
          <Link to="" className='nav-link'> بوابة الموظفين </Link>
        </li>
      </ul>
    </div>
  </div>


  </>

}
