import React from 'react';

import contactImg from '../../assets/images/contact.png'

export default function Contact() {
    return <>
        <div className="contact py-5" id='main4'>
            <div className="container">
                <div className="row">

                    <div className="col-md-7 col-12">
                        <form>
                            <div className="row">
                                <div className="col-12 mb-3">
                                    <h2 className="heading">تواصل معنا  </h2>
                                </div>
                                <div className="col-12 mb-3">
                                    <input className='form-control' type="text" placeholder='الإسم بالكامل' />
                                </div>
                                <div className="col-12 mb-3">
                                    <input className='form-control' type="text" placeholder=' رقم الجوال' />
                                </div>
                                <div className="col-12 mb-3">
                                    <input className='form-control' type="text" placeholder='البريد الإلكترونى ' />
                                </div>
                                <div className="col-12 mb-3">
                                    <textarea className='form-control' name="text" id="ee" rows="5" placeholder='أدخل رسالتك هنا' ></textarea>
                                </div>
                                <div className="col-12 mb-3 text-start">
                                    <button type='submit' className='btn btn-primary'>إرسال</button>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div className="col-md-5 col-12">
                        {/* <img src={contactImg} alt="contactImg" className='w-100'/> */}
                    </div>
                </div>
            </div>
        </div>
    </>

}
